import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { appConfig } from 'src/app/global.config';
import { CryptoService, ImageService } from 'src/app/_services';
import { ZohoService } from 'src/app/_services/zoho.service';

@Component({
  selector: 'app-solution-enterprise',
  templateUrl: './solution-enterprise.component.html',
  styleUrls: ['./solution-enterprise.component.css', './responsive.css']
})
export class SolutionEnterpriseComponent implements OnInit {
  imagePath = this.imageService.getStaticImage('');
  contentLoaded: any = [];
  zohoRegister: FormGroup;
  dashboardUrl = appConfig.dashboardUrl;
  constructor(
    private formBuilder: FormBuilder,
    private zohoService: ZohoService,
    private cryptoService: CryptoService,
    private imageService: ImageService
  ) { }

  ngOnInit(): void {
    this.zohoRegister = this.formBuilder.group({
      email: ["", [Validators.required, Validators.email]]
    })
  }

  async registerZoho() {
    if (!this.zohoRegister.value.email) { return; }
    this.zohoService.registerZoho(this.zohoRegister.value).subscribe(async (res) => {
      console.log(this.zohoRegister.value.email);

      let data = await this.cryptoService.encryptf(this.zohoRegister.value.email);
      window.location.href = this.dashboardUrl + 'register' + `?cta=menu&email=${encodeURIComponent(data)}`, '_blank';

    }, err => {
      console.log(err);
      ;
    });
  }
  getStaticImage(name) {
    return this.imageService.getStaticImage(name);
  }
  loaded(item) {
    return this.contentLoaded[item] = true;
  }

  isLoaded(item) {
    return { visibility: !this.contentLoaded[item] ? 'hidden' : '' }
  }
}
