import { Component, OnInit } from '@angular/core';
import { ImageService } from 'src/app/_services';
import { appConfig } from 'src/app/global.config';

@Component({
  selector: 'app-solution-compliance-requirement',
  templateUrl: './solution-compliance-requirement.component.html',
  styleUrls: ['./solution-compliance-requirement.component.css', './responsive.css']
})
export class SolutionComplianceRequirementComponent implements OnInit {

  dashboardUrl = appConfig.dashboardUrl;
  imagePath = this.imageService.getStaticImage('');
  contentLoaded: any = [];
  constructor(
    private imageService: ImageService
  ) { }

  ngOnInit(): void {
  }

  loaded(item) {
    return this.contentLoaded[item] = true;
  }

  isLoaded(item) {
    return { visibility: !this.contentLoaded[item] ? 'hidden' : '' }
  }

}
