import { Component, OnInit } from '@angular/core';
import { ImageService } from 'src/app/_services';
import { appConfig } from 'src/app/global.config';

@Component({
  selector: 'app-solution-pharma-biotech',
  templateUrl: './solution-pharma-biotech.component.html',
  styleUrls: ['./solution-pharma-biotech.component.css', './responsive.css']
})
export class SolutionPharmaBiotechComponent implements OnInit {
  imagePath = this.imageService.getStaticImage('');
  contentLoaded: any = [];
  dashboardUrl = appConfig.dashboardUrl;
  constructor(
    private imageService: ImageService
  ) { }

  ngOnInit(): void {
  }

  getStaticImage(name) {
    return this.imageService.getStaticImage(name);
  }
  loaded(item) {
    return this.contentLoaded[item] = true;
  }

  isLoaded(item) {
    return { visibility: !this.contentLoaded[item] ? 'hidden' : '' }
  }
}
